import React from "react";

import styled from "styled-components";

const StyledWrapper = styled.div`
  width: 100%;
  height: 100%;
  position: relative;
`;

const ImageContainer = styled.div`
  position: absolute;
  width: 100%;
  height: 100%;
  overflow: hidden;
  z-index: -1;
`;

const ImageFilters = styled.div`
  position: absolute;
  width: 110%;
  height: 110%;
  top: -5%;
  left: -5%;
  filter: blur(3px) brightness(60%) contrast(120%);
`;

const Image = styled.img`
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: right;
`;

const BlurredBackground = ({ children, background, style, containerStyle }) => (
  <StyledWrapper style={style}>
    <ImageContainer>
      <ImageFilters>
        <Image src={background} />
      </ImageFilters>
    </ImageContainer>
    <div style={containerStyle}>{children}</div>
  </StyledWrapper>
);

export { BlurredBackground };



