import React from "react";

import LogoImage from "./assets/img/logo.png";

export const Logo = ({ style }) => (
  <div
    style={{
      zIndex: "1",
      paddingTop: "20px",
      width: "6vw",
      backgroundColor: "#dbd7d7bd",
      borderRadius: "0 0 5px 5px",
      ...style
    }}
  >
    <img
      style={{ marginLeft: "-1vw", width: "8vw", height: "8vw" }}
      src={LogoImage}
    />
  </div>
);



