import React, { Component } from "react";
import { graphql } from "react-apollo";
import gql from "graphql-tag";
import styled from "styled-components";
import moment from "moment";

import GoogleMapReact from "google-map-react";

import { BlurredBackground } from "./BlurredBackground";
import { Menu } from "./Menu";
import { RestaurantMarker } from "./RestaurantMarker";
import { UserMarker } from "./UserMarker";
import { Logo } from "./Logo";

import BackgroundImage from "./assets/img/bg.jpg";

const DEFAULT_LONGITUDE = 16.609;
const DEFAULT_LATITUDE = 49.194;

const Container = styled.div`
  display: flex;
  width: 100vw;
  height: 100vh;
  position: relative;

  @media (max-width: 420px) {
    flex-direction: column;
  }
`;

const MapContainer = styled.div`
  flex: 3;
`;

const MenusContainer = styled.div`
  display: grid;
  grid-template-columns: 1fr 1fr;
`;

class LunchMaster extends Component {
  state = {
    userPosition: null,
    highlightRestaurant: null
  };

  componentWillMount() {
    if ("geolocation" in navigator) {
      navigator.geolocation.watchPosition(position => {
        this.setState({
          userPosition: {
            latitude: position.coords.latitude,
            longitude: position.coords.longitude
          }
        });
      });
    }
  }

  render() {
    const { data: { loading, restaurants, refetch } } = this.props;
    const { userPosition, highlightRestaurant } = this.state;

    console.log(restaurants);
    const filteredRestaurants = restaurants
      ? restaurants
          .map(restaurant => {
            console.log(moment(restaurant.lastWeekMenu[0].date).toDate());
            const dayMenu = restaurant.lastWeekMenu.filter(
              menu =>
                new Date(menu.date).toDateString() === new Date().toDateString()
            );
            return {
              dayMenu: dayMenu.length > 0 ? dayMenu[0] : null,
              ...restaurant
            };
          })
          .filter(restaurant => restaurant.dayMenu != null)
      : [];

    return (
      <Container>
        <Logo style={{ position: "absolute", left: "20px", top: "0" }} />
        <MapContainer>
          <GoogleMapReact
            bootstrapURLKeys={{
              key: "AIzaSyCgf0x0YkNwkqkz5KEVQCivBtSuVhvwBNA",
              region: "cs"
            }}
            defaultCenter={{ lat: DEFAULT_LATITUDE, lng: DEFAULT_LONGITUDE }}
            zoom={18}
            options={() => ({ minZoom: 18, maxZoom: 18 })}
            onChange={({ bounds }) => {
              refetch({
                longitudeFrom: bounds.leftLng,
                longitudeTo: bounds.rightLng,
                latitudeFrom: bounds.bottomLat,
                latitudeTo: bounds.topLat
              });
            }}
          >
            {filteredRestaurants.map(restaurant => (
              <RestaurantMarker
                key={restaurant.id}
                highlighted={restaurant.id === highlightRestaurant}
                onMouseEnter={() => {
                  this.setState({ highlightRestaurant: restaurant.id });
                }}
                onMouseLeave={() => {
                  this.setState({ highlightRestaurant: null });
                }}
                lat={restaurant.position.latitude}
                lng={restaurant.position.longitude}
              />
            ))}
            {userPosition ? (
              <UserMarker
                lng={userPosition.longitude}
                lat={userPosition.latitude}
              />
            ) : null}
          </GoogleMapReact>
        </MapContainer>
        <BlurredBackground
          background={BackgroundImage}
          style={{
            flex: 2,
            display: "flex",
            boxShadow: "-2px 2px 10px 0px rgba(0,0,0,0.6)"
          }}
          containerStyle={{
            flex: 1,
            overflow: "auto"
          }}
        >
          <MenusContainer>
            {filteredRestaurants.map(restaurant => (
              <Menu
                key={restaurant.id}
                title={restaurant.name}
                menu={restaurant.dayMenu.food}
                highlighted={restaurant.id === highlightRestaurant}
                onMouseEnter={() => {
                  this.setState({ highlightRestaurant: restaurant.id });
                }}
                onMouseLeave={() => {
                  this.setState({ highlightRestaurant: null });
                }}
              />
            ))}
          </MenusContainer>
        </BlurredBackground>
      </Container>
    );
  }
}

const LunchMasterGQL = graphql(
  gql`
    query($longitude: Float, $latitude: Float) {
      restaurants(longitude: $longitude, latitude: $latitude) {
        id
        name
        position {
          longitude
          latitude
        }
        lastWeekMenu {
          date
          food {
            name
            price
          }
        }
      }
    }
  `,
  {
    options: props => ({
      variables: {
        longitude: DEFAULT_LONGITUDE,
        latitude: DEFAULT_LATITUDE
      }
    })
  }
)(LunchMaster);

export { LunchMaster, LunchMasterGQL };



