import React from "react";
import styled from "styled-components";

const Container = styled.div`
  padding: 25px;
`;

const Header = styled.h4`
  margin: 0;
  color: ${props => (props.highlighted ? "#fffb3f" : "#ffcc34")};
  text-transform: uppercase;
  font-size: 1em;
  transition: color 0.4s;
`;

const Food = styled.p`
  font-size: 0.8em;
  color: ${props => (props.highlighted ? "#fbffcc" : "#ccc")};
  display: flex;
  transition: color 0.4s;
  margin: 4px 0;
`;

const FoodName = styled.span`
  flex: 1;
  color: inherit;
`;

const FoodPrice = styled.span`
  width: 80px;
  text-align: right;
  color: inherit;
`;

export const Menu = ({
  title,
  menu,
  style,
  highlighted,
  onMouseEnter,
  onMouseLeave
}) => {
  return (
    <Container
      style={style}
      onMouseEnter={onMouseEnter}
      onMouseLeave={onMouseLeave}
    >
      <Header highlighted={highlighted}>{title}</Header>
      <div>
        {menu.map((food, idx) => (
          <Food key={idx} highlighted={highlighted}>
            <FoodName>{food.name}</FoodName>
            <FoodPrice>{Math.round(food.price) + " Kč "}</FoodPrice>
          </Food>
        ))}
      </div>
    </Container>
  );
};



