import React from "react";

import RestaurantIcon from "./assets/img/restaurant.png";

export const RestaurantMarker = ({
  highlighted,
  onMouseEnter,
  onMouseLeave
}) => (
  <div
    style={{
      position: "relative",
      width: highlighted ? "76px" : "55px",
      height: highlighted ? "76px" : "55px",
      transition: "all 300ms ease"
    }}
  >
    <div
      style={{
        position: "absolute",
        width: "100%",
        height: "100%",
        top: "-50%",
        left: "-50%"
      }}
      onMouseEnter={onMouseEnter}
      onMouseLeave={onMouseLeave}
    >
      <img
        style={{
          width: "100%",
          height: "100%"
        }}
        src={RestaurantIcon}
      />
    </div>
  </div>
);



